.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.arrWrap {
  border: 1px solid #000;
  display: inline-block;
}

.arrContentRowItem,
.arrHeaderWrap {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.arrHeaderItem,
.arrContentColItem {
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrap {
  display: flex;
  flex-direction: row;
  padding: 30px;
}

.actionWrap {
  padding: 20px;
}

.funArea {
  width: 600px;
  height: 400px;
  display: flex;
  flex-direction: column;
}

.runBtn {
  line-height: 36px;
  width: 80px;
  text-align: center;
  cursor: pointer;
  margin: 20px;
}

.role {
  padding: 20px;
}

.pre {
  white-space: pre-wrap;
  word-wrap: break-word;
  width: 400px;
}

li {
  margin: 20px;
}

